import type { ChangeEvent, FormEvent, MouseEvent, Ref } from "react"
import { ValidationType } from "@rp/react/common/hooks"
import { Box, InputLabel, TextField } from "@rp/react/web/mui/core"
import { FormButtons, LoginHintText, LoginPasswordField } from "../Login"
import { PASSWORD_HINTS } from "./constants"

type Props = {
  formRef: Ref<HTMLFormElement>
  showPassword: boolean
  validation: ValidationType
  isLoading: boolean
  isSubmitDisabled: boolean
  onInvalid: (e: FormEvent<HTMLFormElement>) => void
  onSubmit: (e: FormEvent<HTMLFormElement>) => void
  handleShowHidePassword: () => void
  handleBackToLogin: () => void
  handleMouseDownPassword: (event: MouseEvent<HTMLButtonElement>) => void
  onNewPasswordChange: (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void
  onConfirmPasswordChange: (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void
}

function NewPasswordForm({
  formRef,
  showPassword,
  isLoading,
  isSubmitDisabled,
  validation,
  onSubmit,
  onInvalid,
  onNewPasswordChange,
  onConfirmPasswordChange,
  handleShowHidePassword,
  handleMouseDownPassword,
  handleBackToLogin,
}: Props) {
  return (
    <form
      id="formPassword"
      ref={formRef}
      onSubmit={onSubmit}
      autoComplete="off"
      onInvalid={onInvalid}
    >
      <InputLabel htmlFor="code">Please enter the reset code sent to your email</InputLabel>
      <TextField
        variant="outlined"
        placeholder="Reset Code"
        required
        name="code"
        type="text"
        autoFocus
        autoComplete="wibble"
      />

      <LoginPasswordField
        placeholder="New Password"
        name="new-password"
        label="New Password"
        autoComplete="new-password"
        showPassword={showPassword}
        handleClickShowPassword={handleShowHidePassword}
        handleMouseDownPassword={handleMouseDownPassword}
        onChange={onNewPasswordChange}
      />
      <Box
        sx={{ display: "flex", flexDirection: "column", justifyContent: "center", marginTop: 1 }}
      >
        {PASSWORD_HINTS.map(({ hint, validationType }) => (
          <LoginHintText
            key={validationType}
            hint={hint}
            validation={validation}
            validationType={validationType}
          />
        ))}
      </Box>
      <LoginPasswordField
        showPassword={showPassword}
        handleClickShowPassword={handleShowHidePassword}
        handleMouseDownPassword={handleMouseDownPassword}
        placeholder="Confirm New Password"
        label="Confirm New Password"
        name="confirm-password"
        autoComplete="nope"
        onChange={onConfirmPasswordChange}
      />
      <Box sx={{ marginTop: 1 }}>
        <LoginHintText
          hint={"Ensure that the passwords entered in both fields match for confirmation."}
          validation={validation}
          validationType={"matchPasswords"}
        />
      </Box>

      <FormButtons
        submitLabel="Change Password"
        handleBackButton={handleBackToLogin}
        isLoading={isLoading}
        submitDisabled={isSubmitDisabled}
      />
    </form>
  )
}

export { NewPasswordForm }
